.button_type_submit_auth {
  background-color: #fff;
  color: #000;
  padding: 0;
  width: 358px;
  
  height: 50px;  
  border-radius: 2px;  
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  border: none;
  opacity: 1;
  margin-top: 216px;
}

.button_type_submit_auth:hover {
  opacity: 0.85;
}



@media screen and (max-width: 400px) {
  .button_type_submit_auth {
    margin-top: 173px;
    width: 260px;
  }
}
